GAAA,SAAA,CAAA,uBAAA,CAAA,CAAA,6CAAA,GAAA,SAAA,CAAA,2BAAA,CAAA,GAAA,SAAA,CAAA,uBAAA,CAAA,CAAA,qCAAA,GAAA,SAAA,CAAA,2BAAA,CAAA,GAAA,SAAA,CAAA,uBAAA,CAAA,CAAA,4CAAA,GAAA,SAAA,CAAA,uBAAA,CAAA,IAAA,2BAAA,CAAA,GAAA,SAAA,CAAA,uBAAA,CAAA,CAAA,oCAAA,GAAA,SAAA,CAAA,uBAAA,CAAA,IAAA,2BAAA,CAAA,GAAA,SAAA,CAAA,uBAAA,CAAA","sourcesContent":["<template>\r\n    <div class=\"container\">\r\n        <div class=\"row justify-content-center\">\r\n            <div class=\"col-12\">\r\n\t\t\t\t<div class=\"row login-wrap p-4 bg-transparent\">\r\n\t\t\t\t\t<!-- <div class=\"col-md-12\">\r\n\t\t\t\t\t\t<div class=\"lwrap text-center p-4\">\r\n\t\t\t\t\t\t\t<img :src=\"logo\" style=\"height: 60px;\" class=\"img-fluid\" alt=\"\">\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t</div> -->\r\n\t\t\t\t\t<div class=\"col-md-6 bg-dark text-center\">\r\n\t\t\t\t\t\t<h1 class=\"p-4\" style=\"color:#fff; font-size: 1.5rem;\">Welcome to FNCCI</h1>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div class=\"col-md-6 border bg-white\">\r\n\t\t\t\t\t\t<div class=\"lwrap p-4\">\r\n\t\t\t\t\t\t\t<template v-if=\"showMessage == true\">\r\n\t\t\t\t\t\t\t\t<p class=\"alert alert-danger alert-dismissible fade show\" role=\"alert\">\r\n\t\t\t\t\t\t\t\t\t{{ message }}\r\n\t\t\t\t\t\t\t\t\t<button type=\"button\" class=\"btn-close\" data-bs-dismiss=\"alert\" aria-label=\"Close\"></button>\r\n\t\t\t\t\t\t\t\t</p>\r\n\t\t\t\t\t\t\t</template>\r\n\t\t\t\t\t\t\t<template v-if=\"showSuccessMessage == true\">\r\n\t\t\t\t\t\t\t\t<p class=\"alert alert-success alert-dismissible fade show\" role=\"alert\">\r\n\t\t\t\t\t\t\t\t\t{{ successMessage }}\r\n\t\t\t\t\t\t\t\t\t<button type=\"button\" class=\"btn-close\" data-bs-dismiss=\"alert\" aria-label=\"Close\"></button>\r\n\t\t\t\t\t\t\t\t</p>\r\n\t\t\t\t\t\t\t</template>\r\n\t\t\t\t\t\t\t<h2>Please Register for Assessment.<br>\r\n\t\t\t\t\t\t\t</h2>\r\n\t\t\t\t\t\t\t<form class=\"rd-form-icon\" @submit.prevent=\"enroll\">\r\n\t\t\t\t\t\t\t\t<div class=\"mb-3\">\r\n\t\t\t\t\t\t\t\t<label for=\"email\" class=\"form-label mb-1\">Email address</label>\r\n\t\t\t\t\t\t\t\t<div class=\"input-group mb-3\">\r\n\t\t\t\t\t\t\t\t\t<div class=\"f-icon\">\r\n\t\t\t\t\t\t\t\t\t\t<i class=\"fas fa-envelope\"></i>\r\n\t\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t\t<input type=\"email\" v-model=\"email\" name=\"email\" required autocomplete=\"email\" autofocus class=\"form-control form-control-lg\" aria-label=\"Email\" id=\"email\">\r\n\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t<div class=\"row\">\r\n\t\t\t\t\t\t\t\t\t<div class=\"col-md-12\">\r\n\t\t\t\t\t\t\t\t\t\t<button type=\"submit\" class=\"btn btn-lg btn-default w-100 btn-submit\"><i class=\"fas fa-key\"></i> Register</button>\r\n\t\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t</div>\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\r\n\t\t\t\t\t\t\t</form>\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from \"axios\";\r\n\r\nexport default {\r\n    name: 'ExaminerLogin',\r\n    data() {\r\n        return {\r\n\t\t\tlogo: '',\r\n\t\t\trootUrl: process.env.MIX_APP_URL,\r\n            email: '',\r\n            showMessage: false,\r\n            message: '',\r\n\t\t\tshowSuccessMessage: false,\r\n\t\t\tsuccessMessage: '',\r\n        }\r\n    },\r\n    methods: {\r\n        enroll() {\r\n            let self = this;\r\n\r\n\t\t\t$('.btn-submit').attr('disabled', 'disabled');\r\n\t\t\t$('.btn-submit').html('<i class=\"fas fa-key\"></i> Registering ...');\r\n\r\n\t\t\tself.showSuccessMessage = false;\r\n\t\t\tself.successMessage = '';\r\n\r\n            axios.post(process.env.MIX_APP_URL+'/api/examiners/enroll', {\r\n\t\t\t\t'email': self.email\r\n\t\t\t}).then(function (response) {\r\n\t\t\t\tif(response.data.status == 'success')\r\n                {\r\n\t\t\t\t\tself.showSuccessMessage = true;\r\n\t\t\t\t\tself.successMessage = response.data.message;\r\n\t\t\t\t\tself.email = '';\r\n                }\r\n\t\t\t\t$('.btn-submit').removeAttr('disabled');\r\n\t\t\t\t$('.btn-submit').html('<i class=\"fas fa-key\"></i> Register');\r\n            })\r\n            .catch(error => {\r\n\t\t\t\t$('.btn-submit').removeAttr('disabled');\r\n\t\t\t\t$('.btn-submit').html('<i class=\"fas fa-key\"></i> Register');\r\n\r\n                self.message = error.response.data.message;\r\n                self.showMessage = true;\r\n            });\r\n        },\r\n    },\r\n\tmounted() {\r\n\t\tthis.logo = this.rootUrl+'/storage/'+window.Laravel.website_logo;\r\n\t}\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.login-wrap .lwrap { padding: 0; }\r\n.form-control {\r\n    opacity: 1 !important;\r\n}\r\nsvg#freepik_stories-online-shopping:not(.animated) .animable {opacity: 0;}svg#freepik_stories-online-shopping.animated #freepik--background-complete--inject-13 {animation: 0.5s 1 forwards cubic-bezier(.36,-0.01,.5,1.38) slideDown;animation-delay: 0s;}svg#freepik_stories-online-shopping.animated #elnc64i9vhzc {animation: 1s 1 forwards linear slideLeft;animation-delay: 0s;}svg#freepik_stories-online-shopping.animated #elqashfcsnce {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.03571428571428571s;opacity: 0}svg#freepik_stories-online-shopping.animated #el3epyznzmgwt {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.07142857142857142s;opacity: 0}svg#freepik_stories-online-shopping.animated #elre0vdjh8azg {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.10714285714285714s;opacity: 0}svg#freepik_stories-online-shopping.animated #eljlduy0tiz8s {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.14285714285714285s;opacity: 0}svg#freepik_stories-online-shopping.animated #el93vxw0o6sj6 {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.17857142857142855s;opacity: 0}svg#freepik_stories-online-shopping.animated #elnqtizqcb2k {animation: 1s 1 forwards linear slideLeft;animation-delay: 0.21428571428571427s;opacity: 0}svg#freepik_stories-online-shopping.ani